import React from 'react'
import { NavLink } from 'react-router-dom'

import s from './Navbar.module.css'

const Navbar = () => {
    return (
        <nav className={s.nav}>
            <div className={s.item}>
                <NavLink to="/arvps" activeClassName={s.activeLink}>Ангарский район водных путей и судоходства (АРВПиС)</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/bsrvps" activeClassName={s.activeLink}>Байкало-Селенгинский район водных путей и судоходства (БСРВПиС)</NavLink>
            </div>
        </nav>
    )
}

export default Navbar
