import React from 'react'
import { NavLink } from 'react-router-dom'

import s from './Branchs.module.css'

const Branchs = (props) => {
    return (
        <div>
            {props.nameOrg}
            <div className={s.item}>
                <NavLink to={props.path +"/1"} activeClassName={s.activeLink}>Техническое оснащение</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to={props.path +"/2"} activeClassName={s.activeLink}>Сотрудники</NavLink>
            </div>
        </div>
    )
}

export default Branchs
